<template>
  <section>
    <h1>登录</h1>
    <form action="#" @submit="login">
      <input
        placeholder="请输入你的姓名"
        type="text"
        name="username"
        v-model="username"
        :class="{ error }"
      />
      <button @click="login" :disabled="!username.trim() || isLoading">
        <LoadingIcon v-if="isLoading"></LoadingIcon>
        <span>登录</span>
      </button>
    </form>
  </section>
</template>

<script setup lang="ts">
import aes from "@/utils/aes";
import { ref } from "vue";
import { genealogyList, type Person } from "@/utils";
import LoadingIcon from "@/icons/LoadingIcon.vue";
import { useRouter } from "vue-router";
const list: Person[] = JSON.parse(aes.decrypt(genealogyList) || "[]");
const username = ref("");
const error = ref(false);
const isLoading = ref(false);
const router = useRouter();
function login(e: Event) {
  e.preventDefault();
  if (!username.value.trim()) return;
  isLoading.value = true;
  if (
    list.some(
      (item) =>
        item.name === username.value ||
        item.pname === username.value ||
        (Array.isArray(item.mate)
          ? item.mate?.some(
              (mate) =>
                mate.name === username.value || mate.pname === username.value
            )
          : item.mate?.name === username.value ||
            item.mate?.pname === username.value)
    )
  ) {
    localStorage.setItem(
      "token",
      aes.encrypt(username.value + "|" + Date.now())
    );
    router.push("/branch");
  } else {
    error.value = true;
    setTimeout(() => {
      error.value = false;
    }, 200);
  }
  isLoading.value = false;
}
</script>

<style scoped lang="scss">
section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
  min-width: 240px;
  max-width: 280px;
  margin: auto;
  margin-top: calc(var(--margin) * -1);
  height: calc(100vh - var(--safe-top) - var(--margin));

  form {
    width: 100%;

    input {
      height: 40px;
      width: 100%;
      margin-top: 20px;
      background-color: var(--gray-200);
      border-radius: var(--radius);
      border: unset;
      outline: unset;
      line-height: 26px;
      padding: 0 12px;

      &.error {
        border: 2px solid red;
        animation: shake 0.2s;

        @keyframes shake {
          0% {
            transform: translateX(0);
          }

          25% {
            transform: translateX(-5px);
          }

          50% {
            transform: translateX(5px);
          }

          75% {
            transform: translateX(-3px);
          }

          90% {
            transform: translateX(1px);
          }

          100% {
            transform: translateX(0);
          }
        }
      }
    }

    button {
      margin-top: 20px;
      padding: 8px;
      width: 100%;
      font-size: 14px;
      border-radius: var(--radius);
      background-color: var(--primary);
      border: unset;
      color: white;
      transition: background-color 0.3s ease;

      display: flex;
      align-items: center;
      justify-content: center;

      &:disabled {
        background-color: var(--primary-light);
      }

      i {
        margin-right: 5px;
        vertical-align: center;
      }
    }
  }
}
</style>
